.card {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  height: 100%;
  background-color: var(--grey_light-color);

  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 30px;
    flex-grow: 1;
  }

  &__title {
    margin: 0;
    margin-bottom: 22px;
    width: 100%;
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: var(--black-color);
  }

  &__price {
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
  }

  &__btn {
    margin-top: auto;
  }

  &-specials {
    position: relative;

    &__discount {
      @include flex-all-center;

      position: absolute;
      top: 30px;
      left: 0;
      width: 92px;
      height: 34px;
      font-weight: 700;
      font-size: 14px;
      line-height: normal;
      color: var(--white-color);
      background-color: var(--secondary-color);
      clip-path: polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
    }

    &__top {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin-bottom: 16px;
      height: 282px;

      &--image {
        display: block;
      }
    }

    &__content {
      padding-top: 0;
    }

    &__couch {
      position: absolute;
      right: 30px;
      bottom: 30px;

      &--image {
        display: block;
      }
    }

    &__price {
      margin-bottom: 23px;

      & ins {
        margin-right: 30px;
        font-size: 18px;
        text-decoration: none;
        color: var(--secondary-color);
      }

      & del {
        color: var(--grey-color);
      }
    }
  }

  &-rating {
    position: relative;

    &__count {
      @include flex-v-center;

      position: absolute;
      left: 30px;
      top: 30px;
      column-gap: 6px;
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--secondary-color);
    }

    &__top {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin-bottom: 16px;
      height: 191px;

      &--image {
        display: block;
      }
    }

    &__content {
      padding-top: 0;
    }

    &__title {
      margin-bottom: 24px;
    }

    &__price {
      margin-bottom: 22px;
      color: var(--grey_shade-color);
    }
  }

  &-categories {
    padding: 30px;

    &__list {
      @include flex;

      column-gap: 20px;
      margin-bottom: 10px;
      transform: translate(-4px, -2px);
    }

    &__link {
      display: block;
      padding: 2px 5px;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: var(--grey-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }
    }

    &__title {
      margin-bottom: 30px;
      line-height: normal;
    }

    &__center {
      margin-bottom: 33px;
      transform: translateX(-30px);

      &--image {
        display: block;
      }
    }

    &__link-category {
      @include flex-v-center;

      position: relative;
      width: 108px;

      &--text {
        position: relative;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--primary-color);
        transition: color 0.3s ease-in-out;

        &::after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 2px;
          background-color: var(--primary-color);
          transform: translate(0, 0) scaleX(0);
          transform-origin: center;
          transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
        }
      }

      &--circle {
        @include flex-all-center;

        position: absolute;
        right: 0;
        border-radius: 100px;
        border: 2px solid var(--primary-color);
        width: 22px;
        height: 22px;
        transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

        & svg {
          width: 18px;
          height: 10px;
          stroke-width: 2px;
          stroke: var(--primary-color);
          fill: transparent;
          transition: stroke 0.3s ease-in-out;
        }
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
      }

      &:focus-visible:not(:hover):not(:active) .card-categories__link-category--text::after {
        outline: none;
        transform: translate(0, 0) scaleX(1);
      }

      &:focus-visible:not(:hover):not(:active) .card-categories__link-category--circle {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:focus-visible:not(:hover):not(:active) .card-categories__link-category--circle svg {
        outline: none;
        stroke: var(--white-color);
      }

      @media (any-hover: hover) {
        &:hover {
          outline: none;
        }

        &:hover .card-categories__link-category--text::after {
          transform: translate(0, 0) scaleX(1);
        }

        &:hover .card-categories__link-category--circle {
          background-color: var(--primary-color);
        }

        &:hover .card-categories__link-category--circle svg {
          stroke: var(--white-color);
        }
      }

      &:active {
        outline: none;
      }

      &:active .card-categories__link-category--text {
        color: var(--primary_shade-color);
      }

      &:active .card-categories__link-category--text::after {
        background-color: var(--primary_shade-color);
        transform: translate(0, 0) scaleX(1);
      }

      &:active .card-categories__link-category--circle {
        border-color: var(--primary_shade-color);
        background-color: var(--primary_shade-color);
      }

      &:active .card-categories__link-category--circle svg {
        stroke: var(--white-color);
      }
    }
  }

  &-useful {
    &__top {
      &--image {
        display: block;
      }
    }

    &__desc {
      margin: 0;
      margin-bottom: 37px;
      font-weight: 400;
      font-size: 24px;
      line-height: normal;
      color: var(--black-color);
    }
  }
}
