.oxford {
  @include sectionMb(70px);
  @include bg-cover;

  padding-top: 153px;
  padding-bottom: 149px;
  background-image: url('../img/oxford-fon.png');

  &__content {
    margin-bottom: 49px;
    width: 508px;
  }

  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 900;
    font-size: 60px;
    line-height: 120%;
    color: var(--gold-color);
  }

  &__descr {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    color: var(--white-color);
  }

  &__btn {
    padding: 21px 30px;
  }
}
