.footer {
  &__top {
    padding-top: 27px;
    padding-bottom: 18px;
    background-color: var(--md_grey-color);

    &--left {
      grid-column: 4 span;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }

    &--socials-list {
      display: flex;
      column-gap: 16px;
      transform: translate(-2px, -4px);
    }

    &--socials-link {
      display: flex;
      padding: 2px;
      transition: fill 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:focus svg {
        fill: var(--white-color);
      }

      &-svg {
        width: 30px;
        height: 30px;
        fill: var(--grey-color);
        transition: fill 0.3s ease-in-out;

        &:hover {
          fill: var(--primary-color);
        }

        &:active {
          fill: var(--primary_shade-color);
        }
      }
    }

    &--logo {
      align-self: flex-start;
    }

    &--right {
      grid-column: 8 span;

      &-list {
        display: flex;
        justify-content: space-between;
      }

      &-item {
        &-title {
          margin: 0;
          margin-bottom: 6px;
          font-weight: 400;
          font-size: 16px;
          line-height: 100%;
          color: var(--black-color);
        }
      }
    }

    &--menu-list {
      display: flex;
      flex-direction: column;
      row-gap: 6px;
      transform: translate(-4px, -2px);
    }

    &--menu-link {
      display: inline-block;
      padding: 2px 5px;
      font-weight: 400;
      font-size: 14px;
      line-height: 100%;
      color: var(--grey-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }
    }
  }

  &__copy {
    &--container {
      padding: 5px 0;
      text-align: center;
    }

    &-text {
      font-weight: 400;
      font-size: 14px;
      line-height: normal;
      color: var(--grey-color);
    }
  }
}
