.feedback {
  @include sectionMb(70px);

  &__left {
    grid-column: 6 span;
  }

  &__title {
    margin-bottom: 9px;
  }

  &__desc {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 14px;
    line-height: normal;
    color: var(--grey_shade-color);
  }

  &__right {
    grid-column: 6 span;
    background-image: url('../img/feedback-fon.png');
    background-position: left center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}
