.form {
  @include flex;

  &-header {
    grid-column: 10 span;
    grid-row: 3;

    &__label {
      @include flex-v-center;

      position: relative;
      width: 100%;
      height: 100%;

      &--svg {
        position: absolute;
        left: 31px;
        margin-right: 6px;
        width: 20px;
        height: 20px;
        fill: var(--grey-color);
      }
    }

    .just-validate-error-label {
      position: absolute;
      left: 60px;
      top: 3px;
      font-weight: 400;
      font-size: 9px;
      line-height: normal;
      color: var(--secondary_shade-color) !important;
    }

    &__input {
      padding-left: 60px;
      padding-right: 36px;
      outline: none;
      border-radius: 10px;
      width: 100%;
      height: 100%;
      background-color: var(--grey_light-color);
      transition: background-color 0.3s ease-in-out;

      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: var(--grey-color);
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 2px solid var(--primary-color);
        background-color: var(--pink_light-color);
      }

      &:focus + .form-header__label--svg {
        fill: var(--primary-color);
      }

      &:hover {
        outline: none;
        background-color: var(--md_grey-color);
      }
    }

    &__btn-search {
      position: absolute;
      right: 36px;
      border-radius: 100px;
      border: 2px solid var(--primary-color);
      width: 32px;
      height: 32px;
      transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
        background-color: var(--primary-color);
      }

      &:active {
        outline: none;
        border-color: var(--primary_shade-color);
        background-color: var(--primary_shade-color);
      }

      &:focus-visible:not(:hover):not(:active) .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &:hover .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &:active .form-header__btn-search--svg {
        stroke: var(--white-color);
      }

      &--svg {
        width: 8px;
        height: 28px;
        stroke-width: 2px;
        stroke: var(--primary-color);
        fill: transparent;
        transition: stroke 0.3s ease-in-out;
      }
    }
  }

  &-feedback {
    flex-direction: column;
    row-gap: 30px;

    &__label {
      @include flex-v-center;

      position: relative;
      width: 100%;
      height: 54px;
    }

    .just-validate-error-label {
      position: absolute;
      left: 20px;
      top: 3px;
      font-weight: 400;
      font-size: 10px;
      line-height: normal;
      color: var(--secondary_shade-color) !important;
    }

    &__input {
      padding-left: 18px;
      border-radius: 10px;
      width: 100%;
      height: 100%;
      font-weight: 400;
      font-size: 16px;
      line-height: 40px;
      color: var(--black-color);
      background-color: var(--grey_light-color);
      transition: background-color 0.3s ease-in-out;

      &::placeholder {
        color: var(--grey-color);
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 2px solid var(--primary-color);
        background-color: var(--pink_light-color);
      }

      &:hover {
        outline: none;
        background-color: var(--md_grey-color);
      }

      &:active {
        outline: 2px solid var(--green_input-color);
      }
    }

    &__btn {
      display: block;
      padding: 17px 33px;
      margin-bottom: 12px;
    }
  }
}

.custom-checkbox {
  &__input {
    position: absolute;
    opacity: 0;
  }

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 22px;
    font-weight: 400;
    font-size: 14px;
    line-height: normal;
    color: var(--black-color);

    &--link {
      color: var(--primary-color);
      text-decoration-line: underline;

      &:focus {
        outline: 1px solid var(--primary-color);
      }
    }
  }

  &__text::before {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    display: inline-block;
    border: 1px solid var(--grey-color);
    border-radius: 2px;
    width: 14px;
    height: 14px;
    transition: background-color 0.3s ease-in-out;
  }

  &__text::after {
    content: '';
    position: absolute;
    left: 0.5px;
    top: 3px;
    width: 14px;
    height: 14px;
    background-image: url('../img/check.svg');
    background-position: center;
    background-size: 10px 8px;
    background-repeat: no-repeat;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &__input:checked + &__text::before {
    border: none;
    background-color: var(--primary-color);
  }

  &__input:checked + &__text::after {
    opacity: 1;
  }

  &__input:focus + &__text::before {
    outline: 1px solid var(--primary-color);
  }
}
